import "./PositionCard.css";
import { useMediaQuery } from "react-responsive";
import TagButton, { TagButtonType } from "../buttons/tag_button/TagButton";
import { MediaQueries } from "../../../assets/constants/enums/MediaQueries";
import { FoodPositionInfo } from "../../../assets/constants/content_types/FoodInfo";
import { useDispatch } from "react-redux";
import { setDisplayedFood } from "../../../redux/displayed_food_reducer/DisplayedFoodReducer";

interface PositionCardProps {
  position: FoodPositionInfo;
  inOrder: boolean;
}

const PositionCard = (props: PositionCardProps) => {
  const dispatch = useDispatch();
  const isMobile = useMediaQuery({ query: MediaQueries.NORMAL_MOBILE });

  return (
    <div
      className="edit-order__item"
      onClick={() => {
        dispatch(setDisplayedFood(props.position));
      }}
    >
      <img
        className="order-img"
        src={props.position.image}
        alt={props.position.name}
      />
      <div className="edit-order__item-cont">
        <div className="edit-order__item-text">
          <div className="edit-order__item-title">{props.position.name}</div>
          <div className="edit-order__item-info">
            <div className="no-wrap">Основное меню</div>
            <div>{props.position.foodVariations[0].price} ₽</div>
          </div>
        </div>
        <div className="edit-order__item-btns">
          {props.inOrder ? (
            <TagButton
              onClickAction={() => {}}
              isActive={false}
              text={"Убрать"}
              withCross={true}
              type={isMobile ? TagButtonType.MOBILE : TagButtonType.DESKTOP}
              fullWidth={isMobile}
            />
          ) : null}
          <TagButton
            onClickAction={() => {}}
            isActive={false}
            text={"+1"}
            type={isMobile ? TagButtonType.MOBILE : TagButtonType.DESKTOP}
          />
          {props.inOrder ? (
            <TagButton
              onClickAction={() => {}}
              isActive={false}
              text={"-1"}
              type={isMobile ? TagButtonType.MOBILE : TagButtonType.DESKTOP}
            />
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default PositionCard;
